// 使用者付款頁面
<template>
  <div>
    <!-- <Loading :active="isLoading"></Loading> -->
    <StepProgress class="step-progress"></StepProgress>
    <div class="my-5 row justify-content-center">
      <form class="col-md-8 w-100" @submit.prevent="payOrder">
        <div class="h1 fw-bold text-center mb-5 text-success">Order bulid successfully !!</div>
        <div class="table-responsive">
          <table class="table align-middle table-success table-striped table-hover check-out-table">
            <thead>
            <th>Item</th>
            <th>Origin Price</th>
            <th>Quantity</th>
            <th class="text-end">Price</th>
            </thead>
            <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td>{{ item.product.title }}</td>
              <td>{{ item.product.price }}/{{item.product.unit}}</td>
              <td>{{ item.qty }}</td>
              <td class="text-end">US$ {{ Math.round(item.final_total*100)/100 }}</td>
            </tr>
            </tbody>
            <tfoot>
            <tr>
              <td colspan="3" class="text-end">Total after discounted</td>
              <td class="text-end">US$ {{ Math.round(order.total*100)/100 }}</td>
            </tr>
            </tfoot>
          </table>
        </div>
        <div class="h1 fw-bold text-center my-5">Your Information</div>
        <div class="table-responsive">
          <table class="table table-striped table-hover check-out-table">
            <tbody>
            <tr>
              <th width="100">Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>Name</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>Phone</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>Address</th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th width="200">Paying Status</th>
              <td>
                <span v-if="!order.is_paid">Need To Pay</span>
                <span v-else class="text-success">Is Paid</span>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
        <div class="text-end d-flex justify-content-between mt-5" v-if="order.is_paid === true">
          <router-link to="/user/index" class="btn btn-primary order-button fw-bold">Look for more nuts</router-link>
          <button class="btn btn-danger order-button fw-bold" disabled>Is Paid</button>
        </div>
        <div class="text-end d-flex justify-content-between mt-5" v-else>
          <router-link to="/user/order" class="btn btn-primary order-button fw-bold">Previous</router-link>
          <button class="btn btn-danger order-button fw-bold">confirm payment</button>
        </div>
        <div v-if="order.is_paid === true" class="h1 text-success fw-bold my-5 text-center">
          Paying successful
          <br>Thank for your purchase !!
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import StepProgress from '../components/StepProgress.vue'
export default {
  data () {
    return {
      order: {
        user: {}
      },
      orderId: '',
      total: 0
      // isLoading: false
    }
  },
  components: {
    StepProgress
  },
  methods: {
    // 取得訂單
    getOrder () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order/${this.orderId}`
      this.$http.get(url)
        .then((res) => {
          if (res.data.success) {
            this.order = res.data.order
          }
        })
    },
    // api確認付款狀態
    payOrder () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/pay/${this.orderId}`
      this.$http.post(url)
        .then((res) => {
          if (res.data.success) {
            this.getOrder()
          }
        })
    },
    // 上色
    StepProgress () {
      const circle1 = document.querySelector('.circle1')
      const circle2 = document.querySelector('.circle2')
      const circle3 = document.querySelector('.circle3')
      const text = document.querySelectorAll('.progress-text')
      const progress = document.getElementById('progress')
      circle1.classList.add('active')
      circle2.classList.add('active')
      circle3.classList.add('active')
      text[0].style.color = '#3158d8'
      text[1].style.color = '#3158d8'
      text[2].style.color = '#3158d8'
      progress.style.width = '100%'
    }
  },
  created () {
    // 透過路由取得orderId
    this.orderId = this.$route.params.orderId
    this.getOrder()
  },
  mounted () {
    this.StepProgress()
  }
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.step-progress{
  @include smallpad(){
    display: none
  }
}
  .check-out-table{
    font-size: 24px;
    @include pad(){
      width: 90%;
      margin: 0 auto;
      overflow-x: auto;
      overflow-y: auto;
    }
  }
</style>
